<template>
  <div class="df aic jcc heightfull">
    <router-link
      :to="item.roleSkipUrl"
      v-for="(item,index) in roleSynthesizeInformation"
      :key="index"
    >
      <el-image :src="item.pictureUrl" style="cursor: pointer;"></el-image>
    </router-link>
  </div>
</template>

<script>
export default {
  name: "home",
  data() {
    return {
      roleSynthesizeInformation: []
    };
  },
  mounted() {
    let userInfo = JSON.parse(localStorage.getItem("userInfo"));
    if (userInfo && userInfo.jwtToken) {
      this.roleSynthesizeInformation = userInfo.roleSynthesizeInformation;
    } else {
      // this.$router.replace("/");
    }
  },
  methods: {}
};
</script>

<style>
</style>


